    }
  },
  computed: { },
  async created () {
    const result = await getArticleChannels()
    console.log('loadChannels', result)
    this.channels = result.data.data.channels
  },
  // created () {
  //   this.loadChannels()
  // },
  methods: {
    hSelectChange (val) {
      this.$emit('aaa', val)
    }
  }
}
